@import "../../styles/variables";

.c-sale {
  background-color: $c-white;
  &__wrapper {
    padding: 100px 0 150px 0;
    margin: 0 345px;
  }
  &__title {
    margin-bottom: 60px;
  }
  &__button {
    margin-top: 80px;
    display: flex;
    justify-content: center;
  }
}

.e-sale__button {
  border: 1px solid $c-dark-green;
  background: none;
  border-radius: 4px;
  padding: 1px 40px;
  // max-width: 209px;
  // width: 100%;
  transition: all 0.5s ease;
  font-weight: bold;
  font-size: 16px;
  line-height: 52px;
  letter-spacing: 0.5px;
  color: $c-dark-green;
  transition: all 0.5s ease;
}
.e-sale__button:hover {
  background: $c-dark-green;
  color: $c-white;
}
