@import "../../styles/variables";

.c-new-arrivals {
  &__wrapper {
    margin-bottom: 60px;
  }
  &__title {
    text-align: center;
  }
}
.e-new-arrivals__title {
  font-weight: 900;
  font-size: 46px;
  line-height: 130%;
  color: $c-grey;
}
.e-new-arrivals__description {
  font-weight: normal;
  font-size: 18px;
  line-height: 150%;
  color: $c-grey-700;
}
.e-new-arrivals__link {
  font-weight: normal;
  font-size: 18px;
  line-height: 150%;
  color: $c-dark-green;
}
