@import "../../../styles/variables";

.c-clothes {
  max-width: 1230px;
  width: 100%;
  margin: 0 345px;
  padding: 32px 0;
  &__wrapper {
    display: flex;
    max-width: 1920px;
    width: 100%;
    justify-content: space-between;
  }
  &__filter {
    max-width: 255px;
    width: 100%;
  }

  &__hide-category,
  &__show-category {
    background: $c-dark-green;
    height: 2px;
    width: 16px;
    position: relative;
    top: 12px;
    left: 0px;
  }

  &__show-category:after {
    content: "";
    height: 16px;
    width: 2px;
    background: $c-dark-green;
    position: absolute;
    left: 6.5px;
    top: -6.5px;
  }
}

.c-clothes__category {
  &-filter {
    max-width: 255px;
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  &-filter-hide {
    display: none;
  }
  &-show-filter {
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
  }
  &--title {
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 160%;
    color: $c-grey;
  }

  &__check {
    max-width: 193px;
    width: 100%;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: $c-grey-800;
    max-width: 407px;
    width: 100%;
    display: flex;
    justify-content: flex-start;
  }
  &__check input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
  }
  &__checkmark {
    position: relative;
    top: 0;
    left: 0;
    margin-top: 2px;
    height: 16px;
    width: 16px;
    margin-right: 12px;
    background-color: $c-white;
    border: 1px solid #17696a;
    border-radius: 3px;
  }
  &__check:hover input ~ &__checkmark {
    background-color: $c-dark-green;
  }
  &__check input:checked ~ &__checkmark {
    background-color: $c-dark-green;
  }
  &__checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }
  &__check input:checked ~ &__checkmark:after {
    display: block;
  }
  &__check &__checkmark:after {
    left: 4px;
    top: 1px;
    width: 3px;
    height: 5px;
    border: solid white;
    border-width: 0 3px 3px 0;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    transform: rotate(45deg);
  }
  &__divider {
    max-width: 255px;
    width: 100%;
    border: 1px solid $c-grey-300;
    margin: 20px 0;
  }
}
.c-clothe__swiper {
  height: 1823px;
}
.c-clothe__swiper > .swiper-wrapper {
  display: grid;
  width: 915px;
  grid-template-columns: 285px 285px 285px;
  justify-content: space-between;
}
