@import "../../styles/variables";

.c-mob-app {
  background-color: $c-grey;
  &__wrapper {
    padding: 60px 0;
    margin: 0 345px 0 317px;
  }
  &__img {
    position: absolute;
    top: 5981px;
    max-width: 490px;
    width: 100%;
  }
  &__right {
    margin-left: 658px;
    max-width: 600px;
    width: 100%;
  }
  &__title {
    margin-bottom: 48px;
  }
  &__links {
    max-width: 348px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  &__link-item-1,
  &__link-item-2 {
    max-width: 162px;
    width: 100%;
    background-color: $c-grey-400;
    border-radius: 4px;
  }
  &__link-item-1 {
    padding: 8px 15px 6px 14px;
  }
  &__link-item-2 {
    padding: 8px 11px 7px 12px;
  }
  &__link-1 {
    max-width: 162px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  &__link-2 {
    display: flex;
    max-width: 162px;
    width: 100%;
    justify-content: space-between;
    flex-direction: row;
  }
}
.e-mob-app__title {
  font-style: normal;
  font-weight: 900;
  font-size: 32px;
  line-height: 130%;
  color: $c-white;
}
.e-mob-app__img {
  max-width: 490px;
  width: 100%;
  overflow: hidden;
}
