@import "../../styles/variables";

.c-instagram {
  background-color: $c-white;
  &__wrapper {
    padding: 0 0 180px 0;
    margin: 0 345px;
    max-width: 1230px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  &__left {
    max-width: 390px;
    width: 100%;
  }
  &__decr {
    margin-bottom: 8px;
  }
  &__title {
    margin-bottom: 60px;
  }
  &__link {
    text-decoration: none;
    max-width: 241px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border: 1px solid $c-dark-green;
    padding: 1px 40px;
    border-radius: 4px;
    font-weight: bold;
    font-size: 16px;
    line-height: 52px;
    letter-spacing: 0.5px;
    color: $c-dark-green;
    transition: all 0.5s ease;
    &-img {
      margin-top: 16px;
    }
  }
  &__link:hover {
    color: $c-white;
    background-color: $c-dark-green;
  }
  &__link:hover svg {
    fill: $c-white;
    stroke: $c-white;
  }
  &__right {
    max-width: 810px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
.e-instagram__descr {
  font-weight: bold;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: $c-grey;
}
