@import "../../styles/variables";

.c-banner {
  max-width: 1920px;
  width: 100%;
  &__wrapper {
    overflow: hidden;
  }

  &-swiper {
    &__slide {
      &__wrapper {
        position: absolute;
        top: 140px;
        padding: 0 345px;
      }
      &__sub-title {
        font-weight: bold;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: $c-grey;
        margin-bottom: 12px;
      }
      &__title {
        font-weight: 900;
        font-size: 72px;
        line-height: 130%;
        letter-spacing: 1px;
        color: $c-grey;
        margin-bottom: 60px;
      }
      &__buttons {
        display: flex;
        max-width: 405px;
        width: 100%;
        justify-content: space-between;
      }
      &__sale-button {
        border: 1px solid $c-dark-green;
        border-radius: 4px;
        background: none;
        padding: 12px 40px;
      }
      &__collection-button {
        background: #17696a;
        border: none;
        border-radius: 4px;
        padding: 12px 40px;
      }
    }
    &-nav-prev {
      position: absolute;
      border-radius: 50%;
      width: 48px !important;
      height: 48px;
      background-color: $c-white;
      top: 538px;
      left: 30px;
      z-index: 1;
      opacity: 0.5;
      background-image: url("../../assets/prevArrow.svg");
      background-position: center;
      background-repeat: no-repeat;
    }
    &-nav-next {
      position: absolute;
      border-radius: 50%;
      width: 48px !important;
      height: 48px;
      background-color: $c-white;
      top: 538px;
      right: 30px;
      z-index: 1;
      opacity: 0.5;
      background-image: url("../../assets/arrowNext.svg");
      background-position: center;
      background-repeat: no-repeat;
    }
    &-nav-prev:hover {
      opacity: 1;
    }

    &-nav-next:hover {
      opacity: 1;
    }
    &__pagination {
      position: absolute;
      top: 728px;
      z-index: 2;
      padding: 0 345px;
      height: 2px;
    }
    &__pagination > .swiper-pagination-bullet {
      max-width: 178px;
      width: 100%;
      background: $c-grey-800;
      height: 2px;
    }
  }
}
.e-collection-button {
  font-weight: bold;
  font-size: 16px;
  line-height: 15px;
  color: $c-white;
}
.e-sale-button {
  font-weight: bold;
  font-size: 16px;
  line-height: 15px;
  color: $c-dark-green;
}
.c-pagination__counter {
  position: relative;
  top: -45px;
  font-weight: bold;
  font-size: 28px;
  line-height: 150%;
  color: $c-grey-800;
}
