@import "../../styles/variables";

.c-services {
  background-color: $c-white;
  &__wrapper {
    padding: 120px 0 180px 0;
    margin: 0 345px;
    max-width: 1230px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}
.c-services-item {
  max-width: 285px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  &__wrapper {
    max-width: 240px;
    width: 100%;
  }
  &__img {
    max-width: 48px;
    width: 100%;
    margin: 0 96px;
    margin-bottom: 24px;
  }
  &__title {
    font-weight: bold;
    font-size: 20px;
    line-height: 150%;
    text-align: center;
    color: $c-grey;
    margin-bottom: 8px;
  }
  &__descr {
    font-weight: normal;
    font-size: 16px;
    line-height: 160%;
    text-align: center;
    color: $c-grey-700;
  }
  &__devider {
    width: 1px;
    height: 136px;
    border: 1px solid rgba(218, 219, 221, 0.5);

    // border: linear-gradient(
    //   270.01deg,
    //   rgba(218, 219, 221, 0) 0%,
    //   rgba(218, 219, 221, 0.5) 12.33%,
    //   #dadbdd 51.91%,
    //   rgba(218, 219, 221, 0.5) 87.85%,
    //   rgba(218, 219, 221, 0) 100%
    // );

    // background: linear-gradient(
    //   270.01deg,
    //   rgba(218, 219, 221, 0) 0%,
    //   rgba(218, 219, 221, 0.5) 12.33%,
    //   #dadbdd 51.91%,
    //   rgba(218, 219, 221, 0.5) 87.85%,
    //   rgba(218, 219, 221, 0) 100%
    // );
    // transform: rotate(90deg);
  }
}
.c-services-item:last-child .c-services-item__devider {
  display: none;
}
.c-services-item:last-child {
  max-width: 240px;
  width: 100%;
  display: block;
}
