@import "../../../styles/variables";

.c-contacts {
  background-color: $c-white;
  &__wrapper {
    padding: 92px 0 180px 0;
    margin: 0 345px;
  }
  &__container {
    max-width: 1230px;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  &__block-tabs {
    max-width: 169px;
    width: 100%;
    height: 186px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
  }
  &__tabs {
    font-weight: bold;
    font-size: 28px;
    line-height: 150%;
    color: $c-grey-600;
    background: none;
    border: none;
  }
  &__active-tabs {
    font-weight: bold;
    font-size: 28px;
    line-height: 150%;
    color: $c-dark-green;
    background: none;
    border: none;
  }
}
.c-contacts__content {
  display: none;
}
