@import "../../styles/variables";

.c-navbar {
  &__wrapper {
    background-color: $c-white;
    padding: 20px 345px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    max-width: 130px;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  &__menu {
    max-width: 348px;
    width: 100%;
    &-list {
      list-style-type: none;
      display: flex;
      justify-content: space-between;
      &__item {
        font-weight: bold;
        font-size: 16px;
        line-height: 160%;
        color: $c-grey-800;
      }
      &__item:last-child {
        color: $c-red;
      }
    }
  }
  &__search {
    max-width: 380px;
    width: 100%;
    display: flex;
    &-input {
      padding: 12px 234px 12px 16px;
      border: 1px solid #d7dadd;
      border-radius: 4px;
      font-weight: normal;
      font-size: 14px;
      line-height: 150%;
    }
    &-img {
      position: relative;
      top: 0px;
      left: -30px;
      max-width: 16px;
      width: 100%;
    }
  }
  &__toolbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 130px;
    width: 100%;
  }
  &__wishlist {
    max-width: 40px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &-img {
      max-width: 19px;
      width: 100%;
    }
    &-counter {
      font-size: 12px;
      line-height: 150%;
      color: $c-grey-800;
    }
  }
  &__cart {
    max-width: 53px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &-img {
      max-width: 21px;
      width: 100%;
    }
    &-counter {
      background: $c-green;
      color: $c-white;
      font-weight: 900;
      font-size: 12px;
      line-height: 150%;
      padding: 1px 8px;
      border-radius: 4px;
    }
  }
}
.e-navbar__logo {
  max-width: 105px;
  width: 100%;
  overflow: hidden;
  &-small {
    max-width: 22px;
    width: 100%;
    overflow: hidden;
  }
}
