@import "../../styles/variables";

.c-banners {
  padding: 180px 0;
  margin: 0 30px;
  &__wrapper {
    display: flex;
    max-width: 1860px;
    width: 100%;
    flex-direction: column;
  }
  &__top {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 16px;
    height: 500px;
  }
  &__bottom {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 500px;
  }
  &__first-banner {
    padding: 60px;
    max-width: 735px;
    width: 100%;
    background-image: url("../../assets/banner1.png");
  }
  &__sec-banner {
    padding: 60px;
    max-width: 1109px;
    width: 100%;
    background-image: url("../../assets/banner2.png");
  }
  &__third-banner {
    padding: 60px;
    max-width: 1109px;
    width: 100%;
    background-image: url("../../assets/banner3.png");
  }
  &__fourth-banner {
    padding: 60px;
    max-width: 735px;
    width: 100%;
    background-color: $c-grey-300;
    &__img {
      max-width: 420px;
      width: 100%;
      overflow: hidden;
      position: relative;
      bottom: 178px;
      right: -256px;
    }
  }
}
.e-banners__button {
  border: 1px solid $c-dark-green;
  background: none;
  border-radius: 4px;
  padding: 1px 32px;
}
.e-banner__button--span {
  font-weight: bold;
  font-size: 14px;
  line-height: 44px;
  letter-spacing: 0.5px;
  color: $c-dark-green;
}
