@import "../../styles/variables";

.c-megamenu {
  border-top: 1px solid #e5e8ed;
  background-color: $c-white;
  position: absolute;
  z-index: 2;
  max-width: 1920px;
  width: 100%;
  box-shadow: 0px 60px 80px -20px rgba(30, 33, 44, 0.16),
    0px 26px 24px -10px rgba(30, 33, 44, 0.1),
    0px 12px 10px -6px rgba(30, 33, 44, 0.08),
    0px 4px 4px -4px rgba(30, 33, 44, 0.05);
  border-radius: 0px 0px 4px 4px;
  &__wrapper {
    padding: 20px 0 40px 0;
    margin: 0 345px;
    max-width: 1230px;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  &__categories {
    list-style-type: none;
    max-width: 180px;
    width: 100%;
    height: 108px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &-item {
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      color: $c-grey-800;
    }
  }
  &__clothes {
    list-style-type: none;
    max-width: 180px;
    width: 100%;
    height: 427px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &-item__link {
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      color: $c-grey-800;
      text-decoration: none;
    }
  }
  &__shoes {
    list-style-type: none;
    max-width: 180px;
    width: 100%;
    height: 282px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &-item {
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      color: $c-grey-800;
      text-decoration: none;
    }
  }
  &__accessories {
    list-style-type: none;
    max-width: 180px;
    width: 100%;
    height: 311px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    &-item {
      font-style: normal;
      font-weight: 400;
      font-size: 14px;
      line-height: 150%;
      color: $c-grey-800;
    }
  }
}
.first-item {
  font-weight: 700;
  font-size: 14px;
  line-height: 150%;
  letter-spacing: 1px;
  color: $c-grey;
  text-decoration: none;
}
