@import "../../styles/variables";

.c-top-categories {
  position: relative;
  z-index: 2;
  top: -120px;

  &__wrapper {
    max-width: 1230px;
    width: 100%;
    margin: 0 345px;
    display: flex;
    justify-content: space-between;
  }
}
.c-category-item {
  max-width: 390px;
  width: 100%;
  display: flex;
  flex-direction: column;
  &__title {
    font-weight: bold;
    font-size: 20px;
    line-height: 150%;
    margin-top: 16px;
    text-align: center;
  }
}
