@import "../../styles/variables";
.c-topbar {
  // max-width: 1920px;
  // width: 100%;
  height: 44px;
  // position: fixed;

  &__wrapper {
    background-color: $c-grey;
    justify-content: space-between;
    flex-direction: row;
    padding: 12px 345px;
    display: flex;
  }
  &__phone {
    text-decoration: none;
    color: $c-white;
    opacity: 0.6;
    font-family: $f-base;
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
  }
  &__menu {
    max-width: 360px;
    width: 100%;
  }
  &__menu-list {
    list-style-type: none;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    &__item > a {
      text-decoration: none;
      color: $c-white;
      opacity: 0.6;
      font-family: $f-base;
      font-size: 14px;
      line-height: 21px;
    }
  }
  &__account {
    max-width: 125px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    &--text {
      color: $c-white;
      opacity: 0.6;
      font-family: $f-base;
      font-size: 14px;
      font-weight: 400;
      line-height: 21px;
    }
    &--img {
      max-width: 13px;
      width: 100%;
      fill: $c-white;
      fill-opacity: 0.6;
    }
  }
}
