@import "../../styles/variables";

.c-popular-categories {
  padding-bottom: 180px;
  margin: 0 345px;
  &__title {
    text-align: center;
    margin-bottom: 60px;
  }
  &__clothes {
    max-width: 1230px;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
}
.c-categories-clothe {
  &__descr {
    margin-top: 24px;
    text-align: center;
    font-weight: bold;
    font-size: 20px;
    line-height: 150%;
    color: $c-grey;
  }
}
